import React, { useState } from 'react'
import 'codemirror/lib/codemirror.css'
import 'codemirror/theme/material.css'
import 'codemirror/mode/xml/xml'
import 'codemirror/mode/javascript/javascript'
import 'codemirror/mode/css/css'
import {FaCompressAlt,FaExpandAlt} from 'react-icons/fa'
import { Controlled as CEditor } from 'react-codemirror2'
export default function Editor({displayTitle,language,onchange,value}) {

    const[open,setOpen]=useState(true);
    function handleEditorChange(editor,data,value){
      onchange(value)
    }
    return (
        <div className={`editor-container ${open?'':'collpased'}`}>
         <div className="editor-title">
             {displayTitle}
             <button onClick={()=>setOpen(prevOpen=>!prevOpen)} className="btn">
{open? <FaCompressAlt/>:<FaExpandAlt/>}

             </button>
             </div> 
             <CEditor
             onBeforeChange={handleEditorChange}
             value={value}
             className="code-mirror-wrapper"
             options={{
                lineWrapping:true,
                lint:true,
                theme:'material',
                mode:language,
                lineNumbers:true
             }}
             />   
        </div>
    )
}
