
import { useEffect, useState } from 'react';
import './App.css';
import Editor from './components/Editor';


function App() {
  const[html,setHtml]=useState('')
   const[css,setCss]=useState('')
    const[js,setJs]=useState('')
    const[srcDoc,setSrcDoc]=useState('')
  useEffect(()=>{
 const timeout=setTimeout(()=>{
   setSrcDoc(`
   <html>
    <body>${html}</body>
    <style>${css}</style>
    <script>${js}</script>
    </html>
   `)
 },1000)
 return ()=>clearTimeout(timeout);
  },[html,css,js])
   
  return (

    <div className="App">
     <div className="pane top-pane">
        <Editor 
        
        language="xml" 
        displayTitle="HTML" 
        onchange={setHtml} 
        value={html}
        />
         <Editor 
         language="css" 
         displayTitle="CSS" 
         onchange={setCss} 
         value={css}
         />
          <Editor 
          language="JS" displayTitle="JS" 
          onchange={setJs} 
          value={js}
          />
</div>
<div className="pane output">
        <iframe title="output"
        srcDoc={srcDoc}
         sandbox="allow-scripts"
         frameBorder="0"
         width="100%"
         height="100%"
        />
</div>
    </div>
  );
}

export default App;
